export default class Api {
    constructor({url, headers}) {
        this._url = url;
        this._token = headers;
    }

    _checkResponse(res) {
        if (res.ok) {
            return res.json();
        }
        return Promise.reject(`Error: ${res.status}`);
    }

//     
    getUserInfo() {
        return fetch(`${this._url}/users/me`, {
            headers: this._token
        })
            .then(this._checkResponse)
    }

//   
    getInitialCards() {
        return fetch(`${this._url}/cards`, {
            headers: this._token
        })
            .then(this._checkResponse)
    }

    // 
    setProfile(data) {
        return fetch(`${this._url}/users/me`, {
            method: "PATCH",
            headers: this._token,
            body: JSON.stringify({
                name: data["name-input"],
                about: data["job-input"]
            })
        })
            .then(this._checkResponse)
    }

    //   
    addNewCard(data) {
        return fetch(`${this._url}/cards`, {
            method: "POST",
            headers: this._token,
            body: JSON.stringify({
                name: data["place-name"],
                link: data["place-link"]
            })
        })
            .then(this._checkResponse)
    }

// 
    removeCard(id) {
        return fetch(`${this._url}/cards/${id}`, {
            method: "DELETE",
            headers: this._token
        })
            .then(this._checkResponse)
    }

// 
    addLike(id) {
        return fetch(`${this._url}/cards/likes/${id}`, {
            method: "PUT",
            headers: this._token
        })
            .then(this._checkResponse)
    }

// 
    removeLike(id) {
        return fetch(`${this._url}/cards/likes/${id}`, {
            method:"DELETE",
            headers:this._token
        })
            .then(this._checkResponse)
    }
}

